.search {
    display: flex;
    justify-content: end;
}

.search_result_list {
    position: absolute;
    z-index: 30;
    list-style: none;
    right: 0;
    left: 0;
    margin-top: 0;
    margin-left: 0;
    padding-left: 1.2em;
    padding-right: 0.2em;
    margin-bottom: 1px;
    border-radius: 0.7em;
    overflow-x: hidden;
    overflow-y: auto;
    max-height: 8em;
}

.active_filters_list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.close {
    color: black;
    float: right;
    padding-right: 5px;
}

.close:hover {
    opacity: 1;
}

form {
    padding-top: 0;
}
